import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import {
  Container,
  Box,
  Typography,
  TextField,
  Button,
  Paper,
  Alert,
  Tabs,
  Tab,
  Divider,
} from '@mui/material';
import '../styles/Login.css';

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [username, setUsername] = useState('');
  const [error, setError] = useState('');
  const [activeTab, setActiveTab] = useState(0);
  const [signupSuccess, setSignupSuccess] = useState(false);
  const { signIn, signUp } = useAuth();
  const navigate = useNavigate();

  const validateForm = () => {
    if (activeTab === 1) { // Sign up validation
      if (!username.trim()) {
        setError('Username is required');
        return false;
      }
      if (username.length < 3) {
        setError('Username must be at least 3 characters long');
        return false;
      }
      if (password !== confirmPassword) {
        setError('Passwords do not match');
        return false;
      }
      if (password.length < 6) {
        setError('Password must be at least 6 characters long');
        return false;
      }
    }
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    if (!validateForm()) return;

    try {
      if (activeTab === 0) {
        const { error } = await signIn(email, password);
        if (error) throw error;
        navigate('/');
      } else {
        const { error } = await signUp(email, password, username);
        if (error) throw error;
        setSignupSuccess(true);
        // Clear form
        setEmail('');
        setPassword('');
        setConfirmPassword('');
        setUsername('');
      }
    } catch (error) {
      setError(error.message);
    }
  };

  const handleTabChange = (e, newValue) => {
    setActiveTab(newValue);
    setError('');
    setSignupSuccess(false);
  };

  if (signupSuccess) {
    return (
      <Container maxWidth="sm">
        <Box sx={{ mt: 8 }}>
          <Paper
            elevation={3}
            sx={{
              p: 4,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              background: 'rgba(26, 26, 26, 0.9)',
              backdropFilter: 'blur(10px)',
              border: '1px solid rgba(255, 215, 0, 0.2)',
            }}
          >
            <Typography component="h1" variant="h4" sx={{ mb: 3, color: '#ffd700' }}>
              Check Your Email
            </Typography>
            <Alert severity="success" sx={{ width: '100%', mb: 3 }}>
              We've sent a verification email to {email}. Please check your inbox and click the verification link to activate your account.
            </Alert>
            <Typography variant="body1" sx={{ color: '#ffffff', mb: 3, textAlign: 'center' }}>
              Once you've verified your email, you can sign in to your account.
            </Typography>
            <Button
              variant="contained"
              onClick={() => {
                setSignupSuccess(false);
                setActiveTab(0);
              }}
              sx={{
                background: 'linear-gradient(45deg, #d4af37 30%, #b8860b 90%)',
                '&:hover': {
                  background: 'linear-gradient(45deg, #b8860b 30%, #d4af37 90%)',
                },
              }}
            >
              Return to Sign In
            </Button>
          </Paper>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 8 }}>
        <Paper
          elevation={3}
          sx={{
            p: 4,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            background: 'rgba(26, 26, 26, 0.9)',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 215, 0, 0.2)',
          }}
        >
          <Typography component="h1" variant="h4" sx={{ mb: 3, color: '#ffd700' }}>
            {activeTab === 0 ? 'Sign In' : 'Sign Up'}
          </Typography>

          <Tabs
            value={activeTab}
            onChange={handleTabChange}
            sx={{ mb: 3 }}
          >
            <Tab label="Sign In" />
            <Tab label="Sign Up" />
          </Tabs>

          {error && (
            <Alert severity="error" sx={{ width: '100%', mb: 2 }}>
              {error}
            </Alert>
          )}

          <Box component="form" onSubmit={handleSubmit} sx={{ width: '100%' }}>
            {activeTab === 1 && (
              <TextField
                margin="normal"
                required
                fullWidth
                id="username"
                label="Username"
                name="username"
                autoComplete="username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                sx={{
                  '& .MuiInputLabel-root': { color: '#ffd700' },
                  '& .MuiOutlinedInput-root': {
                    '& fieldset': { borderColor: '#ffd700' },
                    '&:hover fieldset': { borderColor: '#ffd700' },
                  },
                }}
              />
            )}
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus={activeTab === 0}
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              sx={{
                '& .MuiInputLabel-root': { color: '#ffd700' },
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffd700' },
                  '&:hover fieldset': { borderColor: '#ffd700' },
                },
              }}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              sx={{
                '& .MuiInputLabel-root': { color: '#ffd700' },
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffd700' },
                  '&:hover fieldset': { borderColor: '#ffd700' },
                },
              }}
            />
            {activeTab === 1 && (
              <TextField
                margin="normal"
                required
                fullWidth
                name="confirmPassword"
                label="Confirm Password"
                type="password"
                id="confirmPassword"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                sx={{
                  '& .MuiInputLabel-root': { color: '#ffd700' },
                  '& .MuiOutlinedInput-root': {
                    '& fieldset': { borderColor: '#ffd700' },
                    '&:hover fieldset': { borderColor: '#ffd700' },
                  },
                }}
              />
            )}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{
                mt: 3,
                mb: 2,
                background: 'linear-gradient(45deg, #d4af37 30%, #b8860b 90%)',
                '&:hover': {
                  background: 'linear-gradient(45deg, #b8860b 30%, #d4af37 90%)',
                },
              }}
            >
              {activeTab === 0 ? 'Sign In' : 'Sign Up'}
            </Button>
          </Box>
        </Paper>
      </Box>
    </Container>
  );
}

export default Login; 