import React, { useState, useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import { Container, Grid, Card, CardContent, Typography, Box, CircularProgress, Divider, Link as MuiLink } from '@mui/material';
import { Link } from 'react-router-dom';
import { fetchRSSFeeds } from '../services/newsService';
import '../styles/Home.css';

const Home = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [newsItems, setNewsItems] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const menuItems = [
    { title: 'Squad Builder', path: '/squad-builder' },
    { title: 'Challenges', path: '/challenges' },
    { title: 'Match Discussion', path: '/match-discussion' },
  ];

  useEffect(() => {
    const loadNews = async () => {
      try {
        const feeds = await fetchRSSFeeds();
        setNewsItems(feeds.slice(0, 10)); // Show only the 10 most recent items
        setError(null);
      } catch (err) {
        setError('Failed to load news feeds');
        console.error('Error loading news:', err);
      } finally {
        setIsLoading(false);
      }
    };

    loadNews();
    
    // Refresh news every 5 minutes
    const interval = setInterval(loadNews, 5 * 60 * 1000);
    return () => clearInterval(interval);
  }, []);

  const formatDate = (date) => {
    return new Date(date).toLocaleDateString('en-GB', {
      day: 'numeric',
      month: 'short',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div className="home-container">
      <Header toggleMenu={toggleMenu} isMenuOpen={isMenuOpen} menuItems={menuItems} />
      
      <Container maxWidth="lg" className="main-content">
        <Box sx={{ my: 4 }}>
          <Typography variant="h3" component="h1" gutterBottom align="center">
            Welcome to Regista
          </Typography>
          <Typography variant="h6" component="p" gutterBottom align="center" color="text.secondary">
            Do as you will
          </Typography>
        </Box>

        <Grid container spacing={4}>
          {/* Features Section */}
          <Grid item xs={12} md={8}>
            <Typography variant="h4" gutterBottom sx={{ color: '#d4af37' }}>
              Features
            </Typography>
            <Grid container spacing={3}>
              {menuItems.map((item, index) => (
                <Grid item xs={12} sm={6} key={index}>
                  <Link to={item.path} style={{ textDecoration: 'none' }}>
                    <Card className="feature-card" sx={{ height: '100%' }}>
                      <CardContent>
                        <Typography variant="h5" component="div" gutterBottom>
                          {item.title}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                          {getDescription(item.title)}
                        </Typography>
                      </CardContent>
                    </Card>
                  </Link>
                </Grid>
              ))}
            </Grid>
          </Grid>

          {/* News Section */}
          <Grid item xs={12} md={4}>
            <Typography variant="h4" gutterBottom sx={{ color: '#d4af37' }}>
              Latest News
            </Typography>
            <Card className="news-card">
              <CardContent>
                {isLoading ? (
                  <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
                    <CircularProgress sx={{ color: '#d4af37' }} />
                  </Box>
                ) : error ? (
                  <Typography variant="body1" color="error" align="center">
                    {error}
                  </Typography>
                ) : newsItems.length > 0 ? (
                  newsItems.map((item, index) => (
                    <React.Fragment key={index}>
                      <Box sx={{ mb: 2 }}>
                        <Typography 
                          variant="caption" 
                          color="text.secondary" 
                          sx={{ 
                            display: 'block',
                            mb: 0.5,
                            fontWeight: 'bold'
                          }}
                        >
                          {item.source}
                        </Typography>
                        <MuiLink 
                          href={item.link} 
                          target="_blank" 
                          rel="noopener noreferrer"
                          sx={{ 
                            color: '#d4af37',
                            textDecoration: 'none',
                            '&:hover': {
                              textDecoration: 'underline'
                            }
                          }}
                        >
                          <Typography variant="h6" gutterBottom>
                            {item.title}
                          </Typography>
                        </MuiLink>
                        <Typography 
                          variant="body2" 
                          color="text.secondary"
                          sx={{ mb: 1 }}
                        >
                          {item.contentSnippet?.slice(0, 150)}...
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          {formatDate(item.pubDate)}
                        </Typography>
                      </Box>
                      {index < newsItems.length - 1 && <Divider sx={{ my: 2 }} />}
                    </React.Fragment>
                  ))
                ) : (
                  <Typography variant="body1" color="text.secondary" align="center">
                    No news available at the moment
                  </Typography>
                )}
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Container>

      <Footer />
    </div>
  );
};

const getDescription = (title) => {
  switch (title) {
    case 'Squad Builder':
      return 'Create and manage your perfect football team';
    case 'Challenges':
      return '"Name every member of The Invincibles", etc ';
    case 'Match Discussion':
      return 'Join the conversation about your favorite matches';
    default:
      return '';
  }
};

export default Home;