import React, { useState, useEffect } from 'react';
import { Container, Box, Typography, TextField, Button, Paper, List, ListItem, ListItemText, Card, CardContent, Grid } from '@mui/material';
import Papa from 'papaparse';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../styles/Challenges.css';

const ChallengeFiles = require.context('../data/name_as_many_as_possible', false, /\.csv$/);

const Challenge = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [ChallengeType, setChallengeType] = useState(null);
  const [ChallengeList, setChallengeList] = useState([]);
  const [selectedChallenge, setSelectedChallenge] = useState(null);
  const [ChallengeData, setChallengeData] = useState([]);
  const [timeLimit, setTimeLimit] = useState(5);
  const [timeLeft, setTimeLeft] = useState(timeLimit * 60);
  const [ChallengeStarted, setChallengeStarted] = useState(false);
  const [answer, setAnswer] = useState('');
  const [correctAnswers, setCorrectAnswers] = useState([]);
  const [incorrectAnswers, setIncorrectAnswers] = useState([]);
  const [isChallengeFinished, setIsChallengeFinished] = useState(false);

  const menuItems = [
    { title: 'Squad Builder', path: '/squad-builder' },
    { title: 'Challenges', path: '/Challenges' },
    { title: 'Dream Football', path: '/dream-football' },
  ];

  const removeAccents = (str) => {
    return str.normalize('NFD').replace(/[\u0300-\u036f]/g, '');
  };

  useEffect(() => {
    const Challenges = ChallengeFiles.keys().map((file) => {
      const name = file
        .replace('./', '')
        .replace('.csv', '')
        .replace(/_/g, ' ')
        .split(' ')
        .map(word => word.charAt(0).toUpperCase() + word.slice(1))
        .join(' ');
      return { name, path: file };
    });
    setChallengeList(Challenges);
  }, []);

  const loadChallengeData = (ChallengePath) => {
    Papa.parse(ChallengeFiles(ChallengePath), {
      download: true,
      header: true,
      complete: (result) => {
        setChallengeData(result.data.map(item => ({
          ...item,
          full_answer_normalized: removeAccents(item.full_answer),
          short_answer_normalized: removeAccents(item.short_answer)
        })));
      },
    });
  };

  useEffect(() => {
    if (ChallengeStarted && !isChallengeFinished && correctAnswers.length === ChallengeData.length) {
      setIsChallengeFinished(true);
      setChallengeStarted(false);
    }
  }, [correctAnswers, ChallengeData, ChallengeStarted, isChallengeFinished]);

  useEffect(() => {
    let timer;
    if (ChallengeStarted && !isChallengeFinished && timeLeft > 0) {
      timer = setInterval(() => {
        setTimeLeft((prev) => prev - 1);
      }, 1000);
    } else if (timeLeft === 0 && ChallengeStarted) {
      setIsChallengeFinished(true);
      setChallengeStarted(false);
    }
    return () => clearInterval(timer);
  }, [ChallengeStarted, timeLeft, isChallengeFinished]);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const startChallenge = () => {
    setTimeLeft(timeLimit * 60);
    setChallengeStarted(true);
    setIsChallengeFinished(false);
    setCorrectAnswers([]);
    setIncorrectAnswers([]);
    setAnswer('');
  };

  const handleTimeLimitChange = (e) => {
    const value = Math.max(1, Math.min(10, parseInt(e.target.value) || 5));
    setTimeLimit(value);
  };

  const handleAnswerSubmit = (e) => {
    e.preventDefault();
    if (!answer.trim() || isChallengeFinished) return;

    const normalizedAnswer = removeAccents(answer.trim().toLowerCase());
    const matchingAnswer = ChallengeData.find(
      (item) =>
        item.full_answer_normalized.toLowerCase() === normalizedAnswer ||
        item.short_answer_normalized.toLowerCase() === normalizedAnswer
    );

    if (matchingAnswer && !correctAnswers.some(a => a.full_answer === matchingAnswer.full_answer)) {
      setCorrectAnswers([...correctAnswers, matchingAnswer]);
    } else if (!matchingAnswer && !incorrectAnswers.includes(answer.trim())) {
      setIncorrectAnswers([...incorrectAnswers, answer.trim()]);
    }
    setAnswer('');
  };

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs < 10 ? '0' : ''}${secs}`;
  };

  const capitalizeName = (name) => {
    return name
      .split(' ')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ');
  };

  return (
    <div className="challenge-container">
      <Header toggleMenu={toggleMenu} isMenuOpen={isMenuOpen} menuItems={menuItems} />
      
      <div className="football-decoration"></div>

      <Container maxWidth="md" className="challenge-content">
        <Box sx={{ my: 4 }}>
          <Typography 
            variant="h4" 
            gutterBottom 
            align="center"
            sx={{ 
              color: '#ffd700',
              textShadow: '0 2px 4px rgba(0, 0, 0, 0.3)',
              mb: 4
            }}
          >
            Football Challenges
          </Typography>

          {!ChallengeType && (
            <Box sx={{ mb: 4 }}>
              <Typography variant="h6" gutterBottom>
                Select Challenge Type:
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Card className="challenge-card" onClick={() => setChallengeType('name_many')}>
                    <CardContent>
                      <Typography variant="h6">1. Name as Many as Possible</Typography>
                    </CardContent>
                  </Card>
                </Grid>
                <Grid item xs={12}>
                  <Card className="challenge-card" sx={{ opacity: 0.7 }}>
                    <CardContent>
                      <Typography variant="h6">2. Name the Year it Happened (Coming Soon)</Typography>
                    </CardContent>
                  </Card>
                </Grid>
                <Grid item xs={12}>
                  <Card className="challenge-card" sx={{ opacity: 0.7 }}>
                    <CardContent>
                      <Typography variant="h6">3. Name the Number (Coming Soon)</Typography>
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            </Box>
          )}

          {ChallengeType === 'name_many' && !selectedChallenge && (
            <Box sx={{ mb: 4 }}>
              <Typography variant="h6" gutterBottom>
                Select a Challenge:
              </Typography>
              <Grid container spacing={2}>
                {ChallengeList.map((Challenge, index) => (
                  <Grid item xs={12} key={index}>
                    <Card 
                      className="challenge-card"
                      onClick={() => {
                        setSelectedChallenge(Challenge);
                        loadChallengeData(Challenge.path);
                      }}
                    >
                      <CardContent>
                        <Typography variant="h6">{Challenge.name}</Typography>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            </Box>
          )}

          {ChallengeType === 'name_many' && selectedChallenge && !ChallengeStarted && !isChallengeFinished && (
            <Box sx={{ mb: 4 }}>
              <Typography variant="h5" gutterBottom>
                {selectedChallenge.name}
              </Typography>
              <TextField
                type="number"
                value={timeLimit}
                onChange={handleTimeLimitChange}
                inputProps={{ min: 1, max: 10 }}
                label="Time Limit (minutes)"
                sx={{ mr: 2 }}
              />
              <Button variant="contained" onClick={startChallenge}>
                Start Challenge
              </Button>
            </Box>
          )}

          {(ChallengeStarted || isChallengeFinished) && (
            <>
              <Box sx={{ mb: 2, textAlign: 'center' }}>
                <Typography variant="h5">
                  Time Left: {formatTime(timeLeft)}
                </Typography>
              </Box>

              {!isChallengeFinished && (
                <Box component="form" onSubmit={handleAnswerSubmit} sx={{ mb: 4 }}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <TextField
                      fullWidth
                      value={answer}
                      onChange={(e) => setAnswer(e.target.value)}
                      placeholder="Enter answer"
                      disabled={isChallengeFinished}
                      sx={{ mr: 2 }}
                    />
                    <Typography variant="h6">
                      {correctAnswers.length}/{ChallengeData.length}
                    </Typography>
                  </Box>
                </Box>
              )}

              <Box sx={{ display: 'flex', gap: 2 }}>
                <Paper 
                  sx={{ 
                    p: 2, 
                    flex: 1,
                    background: 'rgba(255, 255, 255, 0.05)',
                    backdropFilter: 'blur(10px)',
                    border: '1px solid rgba(255, 215, 0, 0.2)',
                    boxShadow: '0 4px 20px rgba(0, 0, 0, 0.3)',
                    '& .MuiTypography-h6': {
                      color: '#ffd700'
                    }
                  }}
                >
                  <Typography variant="h6" color="success.main" gutterBottom>
                    Correct Answers ({correctAnswers.length})
                  </Typography>
                  <List>
                    {correctAnswers.map((ans, idx) => (
                      <ListItem key={idx} divider>
                        <ListItemText 
                          primary={capitalizeName(ans.full_answer)}
                          secondary={ans.answer_info_to_display}
                        />
                      </ListItem>
                    ))}
                  </List>
                </Paper>

                <Paper 
                  sx={{ 
                    p: 2, 
                    flex: 1,
                    background: 'rgba(255, 255, 255, 0.05)',
                    backdropFilter: 'blur(10px)',
                    border: '1px solid rgba(255, 215, 0, 0.2)',
                    boxShadow: '0 4px 20px rgba(0, 0, 0, 0.3)',
                    '& .MuiTypography-h6': {
                      color: '#ffd700'
                    }
                  }}
                >
                  <Typography variant="h6" color="error.main" gutterBottom>
                    Incorrect Answers ({incorrectAnswers.length})
                  </Typography>
                  <List>
                    {incorrectAnswers.map((ans, idx) => (
                      <ListItem key={idx}>
                        <ListItemText primary={capitalizeName(ans)} />
                      </ListItem>
                    ))}
                  </List>
                </Paper>
              </Box>

              {isChallengeFinished && (
                <Box sx={{ mt: 4, textAlign: 'center' }}>
                  <Typography variant="h5" gutterBottom>
                    Challenge Finished! Final Score: {correctAnswers.length}/{ChallengeData.length}
                  </Typography>
                  <Button 
                    variant="contained" 
                    onClick={() => {
                      setChallengeStarted(false);
                      setIsChallengeFinished(false);
                      setSelectedChallenge(null);
                      setChallengeType(null);
                    }}
                  >
                    Back to Challenge Selection
                  </Button>
                </Box>
              )}
            </>
          )}
        </Box>
      </Container>

      <Footer />
    </div>
  );
};

export default Challenge;
