import React from 'react';
import { AppBar, Toolbar, Typography, IconButton, Drawer, List, ListItem, ListItemText, Button } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import '../styles/Header.css';

const Header = ({ toggleMenu, isMenuOpen, menuItems }) => {
  const { user, signOut } = useAuth();
  const navigate = useNavigate();
  const items = Array.isArray(menuItems) ? menuItems : [];

  const handleSignOut = async () => {
    await signOut();
    navigate('/');
  };

  // Add Account to menu items if user is logged in
  const menuItemsWithAccount = user 
    ? [...items, { title: 'Account', path: '/account' }]
    : items;

  return (
    <AppBar position="static" sx={{ 
      background: 'linear-gradient(90deg, #1a1a1a 0%, #2c2c2c 100%)',
      boxShadow: '0 2px 10px rgba(0, 0, 0, 0.3)',
      borderBottom: '1px solid rgba(255, 215, 0, 0.2)'
    }}>
      <Toolbar>
        <Typography 
          variant="h6" 
          component="div" 
          sx={{ 
            flexGrow: 1,
            color: '#ffd700',
            fontWeight: 'bold',
            letterSpacing: 1
          }}
        >
          Regista
        </Typography>
        
        {user ? (
          <Button
            color="inherit"
            onClick={handleSignOut}
            sx={{
              color: '#ffd700',
              mr: 2,
              '&:hover': {
                background: 'rgba(255, 215, 0, 0.1)'
              }
            }}
          >
            Sign Out
          </Button>
        ) : (
          <Button
            color="inherit"
            component={Link}
            to="/login"
            sx={{
              color: '#ffd700',
              mr: 2,
              '&:hover': {
                background: 'rgba(255, 215, 0, 0.1)'
              }
            }}
          >
            Sign In
          </Button>
        )}

        <IconButton
          size="large"
          edge="start"
          sx={{ color: '#ffd700' }}
          aria-label="menu"
          onClick={toggleMenu}
        >
          <MenuIcon />
        </IconButton>
      </Toolbar>

      <Drawer
        anchor="right" 
        open={isMenuOpen} 
        onClose={toggleMenu}
        PaperProps={{
          sx: {
            background: 'rgba(26, 26, 26, 0.95)',
            backdropFilter: 'blur(10px)',
            borderLeft: '1px solid rgba(255, 215, 0, 0.2)',
            color: '#ffffff'
          }
        }}
      >
        <List sx={{ width: 250 }}>
          {menuItemsWithAccount.map((item, index) => (
            <ListItem 
              button 
              key={index} 
              component={Link} 
              to={item.path}
              onClick={toggleMenu}
              sx={{
                '&:hover': {
                  background: 'rgba(255, 215, 0, 0.1)'
                },
                color: '#ffd700'
              }}
            >
              <ListItemText primary={item.title} />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </AppBar>
  );
};

export default Header;
