import React, { useState, useEffect } from 'react';
import {
  Container,
  Box,
  Typography,
  Paper,
  List,
  ListItem,
  ListItemText,
  TextField,
  Button,
  Divider,
  Avatar,
  Grid,
  Card,
  CardContent,
  CardActions,
  Snackbar,
  Alert,
} from '@mui/material';
import Header from '../components/Header';
import Footer from '../components/Footer';
import { useAuth } from '../contexts/AuthContext';
import { supabase } from '../services/supabase';
import '../styles/MatchDiscussion.css';

function MatchDiscussion() {
  const { user } = useAuth();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [newComment, setNewComment] = useState('');
  const [newReportTitle, setNewReportTitle] = useState('');
  const [newReportContent, setNewReportContent] = useState('');
  const [showReportForm, setShowReportForm] = useState(false);
  const [comments, setComments] = useState([]);
  const [matchReports, setMatchReports] = useState([]);
  const [selectedMatch, setSelectedMatch] = useState(null);
  const [matches, setMatches] = useState([]);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  const menuItems = [
    { title: 'Home', path: '/' },
    { title: 'Squad Builder', path: '/squad-builder' },
    { title: 'Challenges', path: '/challenges' },
    { title: 'Match Discussion', path: '/match-discussion' },
  ];

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  useEffect(() => {
    fetchMatches();
    if (selectedMatch) {
      fetchComments();
      fetchMatchReports();
    }
  }, [selectedMatch]);

  const fetchMatches = async () => {
    try {
      const { data, error } = await supabase
        .from('match_reports')
        .select('match_date, home_team, away_team, score')
        .order('match_date', { ascending: false });

      if (error) throw error;

      // Get unique matches
      const uniqueMatches = data.reduce((acc, curr) => {
        const matchKey = `${curr.match_date}-${curr.home_team}-${curr.away_team}`;
        if (!acc.find(m => `${m.match_date}-${m.home_team}-${m.away_team}` === matchKey)) {
          acc.push(curr);
        }
        return acc;
      }, []);

      setMatches(uniqueMatches);
      if (uniqueMatches.length > 0 && !selectedMatch) {
        setSelectedMatch(uniqueMatches[0]);
      }
    } catch (error) {
      console.error('Error fetching matches:', error);
      setSnackbar({
        open: true,
        message: 'Failed to load matches',
        severity: 'error'
      });
    }
  };

  const fetchComments = async () => {
    if (!selectedMatch) return;

    try {
      const { data, error } = await supabase
        .from('comments')
        .select(`
          *,
          profiles:user_id (username)
        `)
        .eq('match_report_id', selectedMatch.id)
        .order('created_at', { ascending: true });

      if (error) throw error;
      setComments(data);
    } catch (error) {
      console.error('Error fetching comments:', error);
      setSnackbar({
        open: true,
        message: 'Failed to load comments',
        severity: 'error'
      });
    }
  };

  const fetchMatchReports = async () => {
    if (!selectedMatch) return;

    try {
      const { data, error } = await supabase
        .from('match_reports')
        .select(`
          *,
          profiles:user_id (username)
        `)
        .eq('match_date', selectedMatch.match_date)
        .eq('home_team', selectedMatch.home_team)
        .eq('away_team', selectedMatch.away_team)
        .order('created_at', { ascending: false });

      if (error) throw error;
      setMatchReports(data);
    } catch (error) {
      console.error('Error fetching match reports:', error);
      setSnackbar({
        open: true,
        message: 'Failed to load match reports',
        severity: 'error'
      });
    }
  };

  const handleCommentSubmit = async (e) => {
    e.preventDefault();
    if (!newComment.trim() || !user || !selectedMatch) return;

    try {
      const { error } = await supabase
        .from('comments')
        .insert({
          content: newComment.trim(),
          user_id: user.id,
          match_report_id: selectedMatch.id
        });

      if (error) throw error;
      setNewComment('');
      setSnackbar({
        open: true,
        message: 'Comment posted successfully',
        severity: 'success'
      });
    } catch (error) {
      console.error('Error posting comment:', error);
      setSnackbar({
        open: true,
        message: 'Failed to post comment',
        severity: 'error'
      });
    }
  };

  const handleReportSubmit = async (e) => {
    e.preventDefault();
    if (!newReportTitle.trim() || !newReportContent.trim() || !user || !selectedMatch) return;

    try {
      const { error } = await supabase
        .from('match_reports')
        .insert({
          title: newReportTitle.trim(),
          content: newReportContent.trim(),
          user_id: user.id,
          match_date: selectedMatch.match_date,
          home_team: selectedMatch.home_team,
          away_team: selectedMatch.away_team,
          score: selectedMatch.score
        });

      if (error) throw error;
      setNewReportTitle('');
      setNewReportContent('');
      setShowReportForm(false);
      setSnackbar({
        open: true,
        message: 'Match report posted successfully',
        severity: 'success'
      });
    } catch (error) {
      console.error('Error posting match report:', error);
      setSnackbar({
        open: true,
        message: 'Failed to post match report',
        severity: 'error'
      });
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleString('en-GB', {
      day: 'numeric',
      month: 'short',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  return (
    <div className="match-discussion-container">
      <Header toggleMenu={toggleMenu} isMenuOpen={isMenuOpen} menuItems={menuItems} />
      
      <Container maxWidth="lg">
        <Box sx={{ my: 4 }}>
          <Typography variant="h4" sx={{ color: '#ffd700', mb: 4, textAlign: 'center' }}>
            Match Discussion
          </Typography>

          {/* Match Selection */}
          <Paper sx={{ 
            p: 3, 
            mb: 4,
            background: 'rgba(26, 26, 26, 0.9)',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 215, 0, 0.2)',
          }}>
            <Typography variant="h6" sx={{ color: '#ffd700', mb: 2 }}>
              Select Match
            </Typography>
            <Grid container spacing={2}>
              {matches.map((match) => (
                <Grid item xs={12} sm={6} md={4} key={`${match.match_date}-${match.home_team}-${match.away_team}`}>
                  <Card
                    sx={{
                      cursor: 'pointer',
                      background: selectedMatch?.match_date === match.match_date &&
                        selectedMatch?.home_team === match.home_team &&
                        selectedMatch?.away_team === match.away_team
                        ? 'rgba(255, 215, 0, 0.1)'
                        : 'rgba(26, 26, 26, 0.9)',
                      border: '1px solid rgba(255, 215, 0, 0.2)',
                    }}
                    onClick={() => setSelectedMatch(match)}
                  >
                    <CardContent>
                      <Grid container spacing={2} alignItems="center">
                        <Grid item xs={5} sx={{ textAlign: 'right' }}>
                          <Typography variant="subtitle1" sx={{ color: '#ffd700' }}>
                            {match.home_team}
                          </Typography>
                        </Grid>
                        <Grid item xs={2} sx={{ textAlign: 'center' }}>
                          <Typography variant="h6" sx={{ color: '#ffd700' }}>
                            {match.score}
                          </Typography>
                        </Grid>
                        <Grid item xs={5} sx={{ textAlign: 'left' }}>
                          <Typography variant="subtitle1" sx={{ color: '#ffd700' }}>
                            {match.away_team}
                          </Typography>
                        </Grid>
                      </Grid>
                      <Typography variant="caption" sx={{ color: '#ffffff', display: 'block', textAlign: 'center', mt: 1 }}>
                        {formatDate(match.match_date)}
                      </Typography>
                    </CardContent>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Paper>

          {selectedMatch && (
            <>
              {/* Comments Section */}
              <Paper sx={{ 
                p: 3, 
                mb: 4,
                background: 'rgba(26, 26, 26, 0.9)',
                backdropFilter: 'blur(10px)',
                border: '1px solid rgba(255, 215, 0, 0.2)',
              }}>
                <Typography variant="h5" sx={{ color: '#ffd700', mb: 3 }}>
                  Comments
                </Typography>
                
                <List>
                  {comments.map((comment) => (
                    <React.Fragment key={comment.id}>
                      <ListItem alignItems="flex-start">
                        <Avatar sx={{ bgcolor: '#ffd700', color: '#1a1a1a', mr: 2 }}>
                          {comment.profiles?.username?.charAt(0) || 'U'}
                        </Avatar>
                        <ListItemText
                          primary={
                            <Typography variant="subtitle1" sx={{ color: '#ffd700' }}>
                              {comment.profiles?.username || 'Unknown User'}
                            </Typography>
                          }
                          secondary={
                            <>
                              <Typography variant="body2" sx={{ color: '#ffffff' }}>
                                {comment.content}
                              </Typography>
                              <Typography variant="caption" sx={{ color: '#ffffff', display: 'block', mt: 1 }}>
                                {formatDate(comment.created_at)}
                              </Typography>
                            </>
                          }
                        />
                      </ListItem>
                      <Divider variant="inset" component="li" sx={{ borderColor: 'rgba(255, 215, 0, 0.2)' }} />
                    </React.Fragment>
                  ))}
                </List>

                {user ? (
                  <Box component="form" onSubmit={handleCommentSubmit} sx={{ mt: 3 }}>
                    <TextField
                      fullWidth
                      multiline
                      rows={2}
                      value={newComment}
                      onChange={(e) => setNewComment(e.target.value)}
                      placeholder="Add a comment..."
                      sx={{
                        '& .MuiInputBase-root': {
                          color: '#ffffff',
                        },
                        '& .MuiOutlinedInput-root': {
                          '& fieldset': { borderColor: 'rgba(255, 215, 0, 0.2)' },
                          '&:hover fieldset': { borderColor: 'rgba(255, 215, 0, 0.5)' },
                        },
                      }}
                    />
                    <Button
                      type="submit"
                      variant="contained"
                      sx={{
                        mt: 2,
                        background: 'linear-gradient(45deg, #d4af37 30%, #b8860b 90%)',
                        '&:hover': {
                          background: 'linear-gradient(45deg, #b8860b 30%, #d4af37 90%)',
                        },
                      }}
                    >
                      Post Comment
                    </Button>
                  </Box>
                ) : (
                  <Typography variant="body2" sx={{ color: '#ffffff', mt: 3, textAlign: 'center' }}>
                    Please sign in to post comments
                  </Typography>
                )}
              </Paper>

              {/* Match Reports Section */}
              <Paper sx={{ 
                p: 3,
                background: 'rgba(26, 26, 26, 0.9)',
                backdropFilter: 'blur(10px)',
                border: '1px solid rgba(255, 215, 0, 0.2)',
              }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
                  <Typography variant="h5" sx={{ color: '#ffd700' }}>
                    Match Reports
                  </Typography>
                  {user && (
                    <Button
                      variant="contained"
                      onClick={() => setShowReportForm(!showReportForm)}
                      sx={{
                        background: 'linear-gradient(45deg, #d4af37 30%, #b8860b 90%)',
                        '&:hover': {
                          background: 'linear-gradient(45deg, #b8860b 30%, #d4af37 90%)',
                        },
                      }}
                    >
                      {showReportForm ? 'Cancel' : 'Write Report'}
                    </Button>
                  )}
                </Box>

                {showReportForm && (
                  <Box component="form" onSubmit={handleReportSubmit} sx={{ mb: 4 }}>
                    <TextField
                      fullWidth
                      label="Title"
                      value={newReportTitle}
                      onChange={(e) => setNewReportTitle(e.target.value)}
                      sx={{ mb: 2 }}
                    />
                    <TextField
                      fullWidth
                      multiline
                      rows={4}
                      label="Content"
                      value={newReportContent}
                      onChange={(e) => setNewReportContent(e.target.value)}
                      sx={{ mb: 2 }}
                    />
                    <Button
                      type="submit"
                      variant="contained"
                      sx={{
                        background: 'linear-gradient(45deg, #d4af37 30%, #b8860b 90%)',
                        '&:hover': {
                          background: 'linear-gradient(45deg, #b8860b 30%, #d4af37 90%)',
                        },
                      }}
                    >
                      Submit Report
                    </Button>
                  </Box>
                )}

                {matchReports.map((report) => (
                  <Card key={report.id} sx={{ mb: 3 }}>
                    <CardContent>
                      <Typography variant="h6" sx={{ color: '#ffd700' }}>
                        {report.title}
                      </Typography>
                      <Typography variant="body2" sx={{ color: '#ffffff', mt: 1 }}>
                        {report.content}
                      </Typography>
                      <Typography variant="caption" sx={{ color: '#ffffff', display: 'block', mt: 2 }}>
                        By {report.profiles?.username || 'Unknown User'} • {formatDate(report.created_at)}
                      </Typography>
                    </CardContent>
                  </Card>
                ))}
              </Paper>
            </>
          )}
        </Box>
      </Container>

      <Footer />

      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
      >
        <Alert
          onClose={() => setSnackbar({ ...snackbar, open: false })}
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </div>
  );
}

export default MatchDiscussion; 