import React, { useState, useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../styles/SquadBuilder.css';

const formations = {
  '4-4-2': { goalkeeper: 1, defenders: 4, midfielders: 4, forwards: 2 },
  '4-3-3': { goalkeeper: 1, defenders: 4, midfielders: 3, forwards: 3 },
  '3-4-3': { goalkeeper: 1, defenders: 3, midfielders: 4, forwards: 3 },
  '4-2-1-3': { goalkeeper: 1, defenders: 4, defensiveMidfielders: 2, attackingMidfielder: 1, forwards: 3 },
  '3-5-2': { 
    goalkeeper: 1, 
    defenders: 3, 
    wingers: 2, 
    defensiveMidfielders: 2, 
    attackingMidfielder: 1, 
    forwards: 2 
  },
  '4-1-2-1-2': { goalkeeper: 1, defenders: 4, cdm: 1, wingMidfielders: 2, cam: 1, forwards: 2 },
  '4-3-2-1': { goalkeeper: 1, defenders: 4, centralMidfielders: 3, attackingForwards: 2, forwards: 1 }
};

const positions = ['GK', 'LB', 'CB', 'RB', 'CDM', 'CM', 'LM', 'RM', 'CAM', 'LW', 'RW', 'ST', 'LF', 'RF'];

function SquadBuilder() {
  const [currentFormation, setCurrentFormation] = useState(() => {
    // Get formation from localStorage or default to '4-4-2'
    return localStorage.getItem('currentFormation') || '4-4-2';
  });
  const [players, setPlayers] = useState(generateFormation(currentFormation));
  const [selectedPlayer, setSelectedPlayer] = useState(null);
  const [showWarning, setShowWarning] = useState(false);
  const [showShareModal, setShowShareModal] = useState(false);
  const [shareLink, setShareLink] = useState('');
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const menuItems = [
    { title: 'Home', path: '/' },
    { title: 'Squad Builder', path: '/squad-builder' },
    { title: 'Tactics Board', path: '/tactics-board' },
    { title: 'Player Database', path: '/player-database' }
  ];

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  // Save formation to localStorage when it changes
  useEffect(() => {
    localStorage.setItem('currentFormation', currentFormation);
  }, [currentFormation]);

  function generateFormation(formationType) {
    const formation = formations[formationType];
    let id = 1;
    const newPlayers = {};

    newPlayers.goalkeeper = Array(formation.goalkeeper || 0).fill().map(() => ({
      id: id++,
      name: '',
      position: 'GK'
    }));

    if (formationType === '3-5-2') {
      newPlayers.defenders = Array(formation.defenders || 0).fill().map(() => ({
        id: id++,
        name: '',
        position: 'CB'
      }));

      newPlayers.wingers = [
        { id: id++, name: '', position: 'LM' },
        { id: id++, name: '', position: 'RM' }
      ];

      newPlayers.defensiveMidfielders = Array(formation.defensiveMidfielders || 0).fill().map(() => ({
        id: id++,
        name: '',
        position: 'CDM'
      }));

      newPlayers.attackingMidfielder = [{ id: id++, name: '', position: 'CAM' }];

      newPlayers.forwards = Array(formation.forwards || 0).fill().map(() => ({
        id: id++,
        name: '',
        position: 'ST'
      }));
    } else if (formationType === '4-1-2-1-2') {
      newPlayers.defenders = Array(4).fill().map((_, i) => ({
        id: id++,
        name: '',
        position: i === 0 ? 'LB' : i === 3 ? 'RB' : 'CB'
      }));
      newPlayers.cdm = [{ id: id++, name: '', position: 'CDM' }];
      newPlayers.wingMidfielders = [
        { id: id++, name: '', position: 'LM' },
        { id: id++, name: '', position: 'RM' }
      ];
      newPlayers.cam = [{ id: id++, name: '', position: 'CAM' }];
      newPlayers.forwards = Array(formation.forwards || 0).fill().map(() => ({
        id: id++,
        name: '',
        position: 'ST'
      }));
    } else if (formationType === '4-3-2-1') {
      newPlayers.defenders = Array(4).fill().map((_, i) => ({
        id: id++,
        name: '',
        position: i === 0 ? 'LB' : i === 3 ? 'RB' : 'CB'
      }));
      newPlayers.centralMidfielders = Array(3).fill().map(() => ({
        id: id++,
        name: '',
        position: 'CM'
      }));
      newPlayers.attackingForwards = [
        { id: id++, name: '', position: 'LF' },
        { id: id++, name: '', position: 'RF' }
      ];
      newPlayers.forwards = [{ id: id++, name: '', position: 'ST' }];
    } else if (formationType === '4-2-1-3') {
      newPlayers.defenders = Array(4).fill().map((_, i) => ({
        id: id++,
        name: '',
        position: i === 0 ? 'LB' : i === 3 ? 'RB' : 'CB'
      }));
      newPlayers.defensiveMidfielders = [
        { id: id++, name: '', position: 'CDM' },
        { id: id++, name: '', position: 'CDM' }
      ];
      newPlayers.attackingMidfielder = [{ id: id++, name: '', position: 'CAM' }];
      newPlayers.forwards = Array(formation.forwards || 0).fill().map((_, i) => ({
        id: id++,
        name: '',
        position: i === 0 ? 'LW' : i === 1 ? 'ST' : 'RW'
      }));
    } else {
      newPlayers.defenders = Array(formation.defenders || 0).fill().map((_, i) => ({
        id: id++,
        name: '',
        position: formationType.startsWith('3') 
          ? 'CB' 
          : i === 0 ? 'LB' : i === formation.defenders - 1 ? 'RB' : 'CB'
      }));
      newPlayers.midfielders = Array(formation.midfielders || 0).fill().map((_, i) => {
        return {
          id: id++,
          name: '',
          position: i === 0 ? 'LM' : i === formation.midfielders - 1 ? 'RM' : 'CM'
        };
      });
      newPlayers.forwards = Array(formation.forwards || 0).fill().map((_, i) => ({
        id: id++,
        name: '',
        position: formationType.endsWith('3') && formationType !== '4-2-1-3'
          ? i === 0 ? 'LW' : i === 1 ? 'ST' : 'RW' 
          : 'ST'
      }));
    }

    return newPlayers;
  }

  const handleFormationChange = (e) => {
    if (playersHaveNames()) {
      setShowWarning(true);
    } else {
      setCurrentFormation(e.target.value);
      setPlayers(generateFormation(e.target.value));
    }
  };

  const confirmFormationChange = (newFormation) => {
    setCurrentFormation(newFormation);
    setPlayers(generateFormation(newFormation));
    setShowWarning(false);
  };

  const playersHaveNames = () => {
    return Object.values(players).flat().some(player => player.name !== '');
  };

  const handlePlayerClick = (player) => {
    setSelectedPlayer(player);
  };

  const handleNameChange = (e) => {
    setSelectedPlayer({ ...selectedPlayer, name: e.target.value });
  };

  const handlePositionChange = (e) => {
    setSelectedPlayer({ ...selectedPlayer, position: e.target.value });
  };

  const confirmPlayerChanges = () => {
    setPlayers(prevPlayers => {
      const newPlayers = { ...prevPlayers };
      for (let group in newPlayers) {
        newPlayers[group] = newPlayers[group].map(player =>
          player.id === selectedPlayer.id
            ? { ...player, name: selectedPlayer.name, position: selectedPlayer.position }
            : player
        );
      }
      return newPlayers;
    });
    setSelectedPlayer(null);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      confirmPlayerChanges();
    }
  };

  useEffect(() => {
    const playerNames = document.querySelectorAll('.player-name');
    playerNames.forEach(name => {
      const textLength = name.textContent.length;
      if (textLength > 10) {
        const newSize = Math.max(12 - (textLength - 10) * 0.5, 6);
        name.style.fontSize = `${newSize}px`;
      } else {
        name.style.fontSize = '12px';
      }
    });
  }, [players]);

  const getPositionStyle = (position, formationType) => {
    if (formationType === '3-5-2') {
      switch (position) {
        case 'goalkeeper': return { top: '80%' };
        case 'defenders': return { top: '60%' };
        case 'wingers': return { top: '40%' };
        case 'defensiveMidfielders': return { top: '45%' };
        case 'attackingMidfielder': return { top: '25%' };
        case 'forwards': return { top: '10%' };
        default: return {};
      }
    } else if (formationType === '4-1-2-1-2') {
      switch (position) {
        case 'goalkeeper': return { top: '80%' };
        case 'defenders': return { top: '60%' };
        case 'cdm': return { top: '50%' };
        case 'wingMidfielders': return { top: '40%' };
        case 'cam': return { top: '30%' };
        case 'forwards': return { top: '15%' };
        default: return {};
      }
    } else if (formationType === '4-3-2-1') {
      switch (position) {
        case 'goalkeeper': return { top: '80%' };
        case 'defenders': return { top: '60%' };
        case 'centralMidfielders': return { top: '45%' };
        case 'attackingForwards': return { top: '25%' };
        case 'forwards': return { top: '15%', justifyContent: 'center' };
        default: return {};
      }
    } else if (formationType === '4-2-1-3') {
      switch (position) {
        case 'goalkeeper': return { top: '80%' };
        case 'defenders': return { top: '60%' };
        case 'defensiveMidfielders': return { top: '45%' };
        case 'attackingMidfielder': return { top: '30%' };
        case 'forwards': return { top: '15%' };
        default: return {};
      }
    } else {
      const index = ['goalkeeper', 'defenders', 'midfielders', 'forwards'].indexOf(position);
      return { top: `${(3 - index) * 25 + (index === 3 ? 5 : 0)}%` };
    }
  };

  const handleShareSquad = () => {
    const squadData = {
      formation: currentFormation,
      players: players
    };
    
    const encodedData = btoa(JSON.stringify(squadData));
    
    const link = `${window.location.origin}${window.location.pathname}?squad=${encodedData}`;
    setShareLink(link);
    setShowShareModal(true);
  };

  const copyToClipboard = () => {
    navigator.clipboard.writeText(shareLink);
  };

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const squadParam = urlParams.get('squad');
    
    if (squadParam) {
      try {
        const decodedData = JSON.parse(atob(squadParam));
        setCurrentFormation(decodedData.formation);
        setPlayers(decodedData.players);
      } catch (error) {
        console.error('Error loading squad from URL:', error);
      }
    }
  }, []);

  return (
    <div className="squad-builder-page">
      <Header 
        toggleMenu={toggleMenu} 
        isMenuOpen={isMenuOpen} 
        menuItems={menuItems} 
      />
      <main className="squad-builder-content">
        <div className="app-header">
          <h1>Football Formation Manager</h1>
          <div className="header-controls">
            <div className="formation-selector">
              <label htmlFor="formation">Select Formation: </label>
              <select
                id="formation"
                value={currentFormation}
                onChange={handleFormationChange}
              >
                {Object.keys(formations).map(formation => (
                  <option key={formation} value={formation}>
                    {formation}
                  </option>
                ))}
              </select>
            </div>
            <button className="share-btn" onClick={handleShareSquad}>
              Share Squad
            </button>
          </div>
        </div>

        <div className="pitch-container">
          <div className={`pitch formation-${currentFormation}`}>
            <div className="penalty-box"></div>
            <div className="six-yard-box"></div>
            <div className="penalty-arc"></div>
            {Object.entries(players).map(([position, playerList]) => {
              const style = getPositionStyle(position, currentFormation);
              console.log(`Position: ${position}, Formation: ${currentFormation}, Style:`, style);
              return (
                <div
                  key={position}
                  className={`position-row ${position} ${
                    currentFormation === '4-4-2' && (position === 'forwards' || position === 'midfielders')
                      ? 'aligned'
                      : ''
                  }`}
                  style={style}
                >
                  {playerList.map(player => (
                    <div
                      key={player.id}
                      className="player"
                      onClick={() => handlePlayerClick(player)}
                    >
                      <div className="player-kit">
                        <span className="player-position">{player.position}</span>
                        <span className="player-name">
                          {player.name || 'Click to name'}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              );
            })}
          </div>
        </div>

        {selectedPlayer && (
          <div className="modal">
            <div className="modal-content" onKeyPress={handleKeyPress}>
              <h3>Player Details</h3>
              <input
                type="text"
                value={selectedPlayer.name}
                onChange={handleNameChange}
                placeholder="Enter player name"
                className="name-input"
                autoFocus
              />
              <select
                value={selectedPlayer.position}
                onChange={handlePositionChange}
                className="position-select"
              >
                {positions.map(pos => (
                  <option key={pos} value={pos}>
                    {pos}
                  </option>
                ))}
              </select>
              <button onClick={confirmPlayerChanges} className="confirm-btn">
                Confirm
              </button>
              <button onClick={() => setSelectedPlayer(null)} className="cancel-btn">
                Cancel
              </button>
            </div>
          </div>
        )}

        {showWarning && (
          <div className="modal">
            <div className="modal-content warning">
              <h3>Warning</h3>
              <p>Changing formation will clear all player names. Are you sure?</p>
              <button
                onClick={() => confirmFormationChange(document.getElementById('formation').value)}
                className="confirm-btn"
              >
                Yes
              </button>
              <button onClick={() => setShowWarning(false)} className="cancel-btn">
                No
              </button>
            </div>
          </div>
        )}

        {showShareModal && (
          <div className="modal">
            <div className="modal-content">
              <h3>Share Your Squad</h3>
              <p>Copy this link to share your squad with others:</p>
              <div className="share-link-container">
                <input 
                  type="text" 
                  value={shareLink} 
                  readOnly 
                  className="share-link-input"
                />
                <button onClick={copyToClipboard} className="copy-btn">
                  Copy
                </button>
              </div>
              <button onClick={() => setShowShareModal(false)} className="close-btn">
                Close
              </button>
            </div>
          </div>
        )}
      </main>
      <Footer />
    </div>
  );
}

export default SquadBuilder;