import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import {
  Container,
  Box,
  Typography,
  Paper,
  Avatar,
  Button,
} from '@mui/material';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../styles/Account.css';

function Account() {
  const { user, signOut } = useAuth();
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const menuItems = [
    { title: 'Home', path: '/' },
    { title: 'Squad Builder', path: '/squad-builder' },
    { title: 'Challenges', path: '/challenges' },
    { title: 'Account', path: '/account' },
  ];

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  if (!user) {
    return null; // or redirect to login
  }

  return (
    <div className="account-container">
      <Header toggleMenu={toggleMenu} isMenuOpen={isMenuOpen} menuItems={menuItems} />
      
      <Container maxWidth="md">
        <Box sx={{ my: 4 }}>
          <Paper
            elevation={3}
            sx={{
              p: 4,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              background: 'rgba(26, 26, 26, 0.9)',
              backdropFilter: 'blur(10px)',
              border: '1px solid rgba(255, 215, 0, 0.2)',
            }}
          >
            <Avatar
              sx={{
                width: 100,
                height: 100,
                mb: 2,
                bgcolor: '#ffd700',
                color: '#1a1a1a',
                fontSize: '2.5rem',
              }}
            >
              {user.user_metadata?.username?.charAt(0).toUpperCase() || user.email.charAt(0).toUpperCase()}
            </Avatar>

            <Typography variant="h4" sx={{ color: '#ffd700', mb: 2 }}>
              {user.user_metadata?.username || 'User'}
            </Typography>

            <Typography variant="body1" sx={{ color: '#ffffff', mb: 3 }}>
              {user.email}
            </Typography>

            <Button
              variant="contained"
              onClick={signOut}
              sx={{
                background: 'linear-gradient(45deg, #d4af37 30%, #b8860b 90%)',
                '&:hover': {
                  background: 'linear-gradient(45deg, #b8860b 30%, #d4af37 90%)',
                },
              }}
            >
              Sign Out
            </Button>
          </Paper>
        </Box>
      </Container>

      <Footer />
    </div>
  );
}

export default Account; 