const RSS_FEEDS = [
  {
    url: 'https://www.theguardian.com/football/rss',
    name: 'The Guardian'
  },
  {
    url: 'https://www.footballextras.net/feed/',
    name: 'Football Extras'
  },
  {
    url: 'https://feeds.bbci.co.uk/sport/football/rss.xml',
    name: 'BBC Sport'
  }
];

const CORS_PROXY = 'https://api.allorigins.win/raw?url=';

const parseXML = async (xmlText) => {
  const parser = new DOMParser();
  const xmlDoc = parser.parseFromString(xmlText, 'text/xml');
  const items = xmlDoc.querySelectorAll('item');
  
  return Array.from(items).map(item => ({
    title: item.querySelector('title')?.textContent || '',
    link: item.querySelector('link')?.textContent || '',
    contentSnippet: item.querySelector('description')?.textContent?.replace(/<[^>]*>/g, '').slice(0, 150) || '',
    pubDate: new Date(item.querySelector('pubDate')?.textContent || ''),
  }));
};

const fetchRSSFeeds = async () => {
  try {
    const feedPromises = RSS_FEEDS.map(async (feed) => {
      try {
        const response = await fetch(`${CORS_PROXY}${encodeURIComponent(feed.url)}`);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const xmlText = await response.text();
        const items = await parseXML(xmlText);
        
        return items.map(item => ({
          ...item,
          source: feed.name,
        }));
      } catch (error) {
        console.error(`Error fetching ${feed.name} feed:`, error);
        return [];
      }
    });

    const feedResults = await Promise.all(feedPromises);
    const allItems = feedResults.flat();

    // Sort by publication date, most recent first
    return allItems.sort((a, b) => b.pubDate - a.pubDate);
  } catch (error) {
    console.error('Error fetching RSS feeds:', error);
    return [];
  }
};

export { fetchRSSFeeds }; 